import Vue from 'vue'
import Vuex from 'vuex'
import usersAPI from './apis/users'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentUser: {
      id: -1,
      StoreId: '',
      role: '',
      name: '',
      email: '',
      address: '',
      password: '',
      emailVerf: false,
      resetPasswordToken: '',
      resetPasswordExpires: '',
      image: ''
    },
    isAuthenticated: false,
    token: '',
    cartItemNumber: 0
  },
  mutations: {
    setCartItemNumber(state) {
      const localStorageNumber = Number(localStorage.getItem('cartItemNumber'))

      if (localStorageNumber > 0) return state.cartItemNumber = localStorageNumber
      else {
        state.cartItemNumber = 0
        localStorage.removeItem('cartItemNumber')
      }
    },
    setCurrentUser(state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        //  API  currentUser  Vuex state  currentUser
        ...currentUser
      }
      //  token  state 
      state.token = localStorage.getItem('token')
      //  true
      state.isAuthenticated = true
    },
    revokeAuthentication(state) {
      state.currentUser = {}
      state.isAuthenticated = false
      //  state  token
      state.token = ''
      localStorage.removeItem('token')
    }
  },
  actions: {
    fetchCartItemNumber({ commit }) {
      commit('setCartItemNumber')
    },
    async fetchCurrentUser({ commit }) {
      try {
        //  usersAPI.getCurrentUser()  response 
        const { data, statusText } = await usersAPI.getCurrentUser()

        if (statusText !== 'OK') {
          throw new Error(statusText)
        }

        commit('setCurrentUser', {
          id: data.id,
          name: data.name,
          email: data.email,
          image: data.image,
          role: data.role
        })
        return true
      } catch (error) {
        commit('revokeAuthentication')
        return false
      }
    }
  }
})
