import { apiHelper } from './../utils/helpers'

export default {
  // 
  logIn({ email, password, redirectUrl }) {
    //  return  Promise
    return apiHelper.post('/users/logIn', {
      email,
      password,
      redirectUrl
    })
  },
  signUp({ name, email, password, password_confirm }) {
    //  return  Promise
    return apiHelper.post('/users/signUp', {
      name,
      email,
      password,
      password_confirm
    })
  }
}